* {
    font-family: Helvetica, Arial, sans-serif;
}

body {
    padding: 40px;
}

h5 {
    font-weight: lighter;
}

input {
    color:gray;
    width: 100%;
    font-size: 1.2rem;
}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

}


.card {
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;
}

.card p {
    font-family: Arial, Helvetica, sans-serif;
    font-size: small;
    padding: 5px;
    text-align: center;
}

.card img {
    max-width: 170px;
}